input[type=password],
input[type=email] {
    width: 100%;
    padding: 15px 0 15px 8px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
}

input[type=password]:focus,
input[type=email]:focus {
    background-color: #ddd;
    outline: none;
}

button {
    color: white;
    padding: 14px 20px;
    background-color: #04AA6d;
    border: none;
    cursor: poiter;
    margin: 8px 0;
    opacity: 0.8;
}

button:hover {
    opacity: 1;
}

.container1 {
    padding: 16px;
}

.model1 {
    /* position: fixed; */
    z-index: 1;
    width: 100%;
    height: 100%;
    /* background-color: #474e5d; */
    background-color: #f8f9fa;
    /* top: 0; */
    left: 0;
    padding-top: 70px;
    overflow: auto;
}

.model-content1 {
    background-color: #fefefe;
    margin: 5% auto 23% auto;
    border: 1px solid #888;
    width: 30%;
}

hr {
    margin-bottom: 25px;
    border: 1px solid #f1f1f1;
}