input[type=text],
input[type=password],
input[type=email],
input[type=number] {
    width: 100%;
    padding: 15px 0 15px 8px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
}

input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus,
input[type=number]:focus {
    background-color: #ddd;
    outline: none;
}

button {
    color: white;
    padding: 14px 20px;
    background-color: #04AA6d;
    border: none;
    cursor: poiter;
    margin: 8px 0;
    opacity: 0.8;
}

button:hover {
    opacity: 1;
}

.container {
    padding: 16px;
}

.model {
    /* position: fixed; */
    z-index: 1;
    width: 100%;
    height: 100%;
    /* background-color: #474e5d; */
    background-color: #f8f9fa;
    /* top: 0; */
    left: 0;
    padding-top: 50px;
    overflow: auto;
}

.model-content {
    background-color: #fefefe;
    margin: 2% auto 10% auto;
    border: 1px solid #888;
    width: 30%;
}

hr {
    margin-bottom: 25px;
    border: 1px solid #f1f1f1;
}